:root {
  --blue-color: #00acdc;
}

body {
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
}

.header {
  box-sizing: border-box;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 0 50px;
  display: flex;
}

.nav {
  justify-content: flex-end;
  display: flex;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  margin-right: 20px;
}

.list__item:last-child {
  margin-right: 0;
}

.nav__link {
  text-transform: uppercase;
  color: #000;
  align-items: center;
  height: 60px;
  text-decoration: none;
  display: flex;
  position: relative;
}

.is-active, .nav__link:hover {
  color: var(--blue-color);
}

.is-active:after {
  content: "";
  background-color: var(--blue-color);
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}
